.BackBtn {
    height: 2rem;
    width: 4rem;
    border-radius: 3rem;
    text-decoration: none;
    outline: none;
    -webkit-box-shadow: 0 .5rem 1rem rgba(0, 0, 0, 0.7);
            box-shadow: 0 .5rem 1rem rgba(0, 0, 0, 0.7);
    border: .1rem solid black;
    background-image: -webkit-gradient(linear, right bottom, left top, from(rgba(22, 60, 184, 0.9)), to(rgba(40, 155, 69, 0.9)));
    background-image: -webkit-linear-gradient(right bottom, rgba(22, 60, 184, 0.9), rgba(40, 155, 69, 0.9));
    background-image: -o-linear-gradient(right bottom, rgba(22, 60, 184, 0.9), rgba(40, 155, 69, 0.9));
    background-image: linear-gradient(to left top, rgba(22, 60, 184, 0.9), rgba(40, 155, 69, 0.9));
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    font-weight: 600;
}

.BackBtn:hover {
    background-image: -webkit-gradient(linear, right bottom, left top, from(rgba(22, 60, 184, 1)), to(rgba(40, 155, 69, 1)));
    background-image: -webkit-linear-gradient(right bottom, rgba(22, 60, 184, 1), rgba(40, 155, 69, 1));
    background-image: -o-linear-gradient(right bottom, rgba(22, 60, 184, 1), rgba(40, 155, 69, 1));
    background-image: linear-gradient(to left top, rgba(22, 60, 184, 1), rgba(40, 155, 69, 1));
    -webkit-transform: translateY(-.3rem);
        -ms-transform: translateY(-.3rem);
            transform: translateY(-.3rem);
}

.BackBtn:active {
    -webkit-box-shadow: 0 .25rem .5rem rgba(0, 0, 0, 0.9);
            box-shadow: 0 .25rem .5rem rgba(0, 0, 0, 0.9);
    -webkit-transform: translateY(-2px);
        -ms-transform: translateY(-2px);
            transform: translateY(-2px);
}


