.button-list {
    
}

.button {
    width: 3rem;
    height: 3rem;
    outline: none;
    text-decoration: none;
    border-radius: 1rem;
    -webkit-box-shadow: 0 .5rem 1rem rgba(0, 0, 0, 0.7);
            box-shadow: 0 .5rem 1rem rgba(0, 0, 0, 0.7);
    border: .1rem solid black;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    background-image: -webkit-gradient(linear, right bottom, left top, from(rgba(22, 60, 184, 0.7)), to(rgba(40, 155, 69, 0.7)));
    background-image: -webkit-linear-gradient(right bottom, rgba(22, 60, 184, 0.7), rgba(40, 155, 69, 0.7));
    background-image: -o-linear-gradient(right bottom, rgba(22, 60, 184, 0.7), rgba(40, 155, 69, 0.7));
    background-image: linear-gradient(to left top, rgba(22, 60, 184, 0.7), rgba(40, 155, 69, 0.7));
    font-weight: 600;
    margin: .25rem;
}

.button:hover {
    background-image: -webkit-gradient(linear, right bottom, left top, from(rgba(22, 60, 184, 1)), to(rgba(40, 155, 69, 1)));
    background-image: -webkit-linear-gradient(right bottom, rgba(22, 60, 184, 1), rgba(40, 155, 69, 1));
    background-image: -o-linear-gradient(right bottom, rgba(22, 60, 184, 1), rgba(40, 155, 69, 1));
    background-image: linear-gradient(to left top, rgba(22, 60, 184, 1), rgba(40, 155, 69, 1));
    -webkit-transform: translateY(-.3rem);
        -ms-transform: translateY(-.3rem);
            transform: translateY(-.3rem);
}

.button:active {
    -webkit-box-shadow: 0 .25rem .5rem rgba(0, 0, 0, 0.9);
            box-shadow: 0 .25rem .5rem rgba(0, 0, 0, 0.9);
    -webkit-transform: translateY(-2px);
        -ms-transform: translateY(-2px);
            transform: translateY(-2px);
}

@media (max-width: 400px) {
    .button {
        width: 1.5rem;
        height: 1.5rem;
        margin: .15rem;
        font-size: 10px;
        text-align: center;
        justify-self: center;
    }

    .button-list {
        display: grid; 
        grid-template-rows: repeat(2, -webkit-max-content); 
        grid-template-rows: repeat(2, max-content);
        justify-items: center;
    }
}


