.App {
  margin: 0;
  padding: 0;
  font-size: 62.5%;
}

.application {
  padding: 10rem;
  background-image: -webkit-gradient(linear, right bottom, left top, from(rgba(22, 60, 184, 0.8)), to(rgba(40, 155, 69, 0.8))), url(../assets/img/river-landscape.jpg);
  background-image: -webkit-linear-gradient(right bottom, rgba(22, 60, 184, 0.8), rgba(40, 155, 69, 0.8)), url(../assets/img/river-landscape.jpg);
  background-image: -o-linear-gradient(right bottom, rgba(22, 60, 184, 0.8), rgba(40, 155, 69, 0.8)), url(../assets/img/river-landscape.jpg);
  background-image: linear-gradient(to left top, rgba(22, 60, 184, 0.8), rgba(40, 155, 69, 0.8)), url(../assets/img/river-landscape.jpg);
  background-size: cover;
  height: 75vh;

  display: grid;
  grid-template-rows: 20rem;
  grid-template-columns: 40rem;
  -ms-flex-pack: center;
      justify-content: center;
}

@media (max-width: 600px) {
  .application {
    background-image: -webkit-gradient(linear, right bottom, left top, from(rgba(22, 60, 184, 0.8)), to(rgba(40, 155, 69, 0.8)));
    background-image: -webkit-linear-gradient(right bottom, rgba(22, 60, 184, 0.8), rgba(40, 155, 69, 0.8));
    background-image: -o-linear-gradient(right bottom, rgba(22, 60, 184, 0.8), rgba(40, 155, 69, 0.8));
    background-image: linear-gradient(to left top, rgba(22, 60, 184, 0.8), rgba(40, 155, 69, 0.8));

    background-size: none;
    width: 100%;
    height: 100vh;
  }
}

.timer-app {
  background-color: white;
  opacity: .75;
  border-radius: 1rem;
  
  display: grid;
  grid-template-rows: repeat(4, auto);
  -ms-flex-pack: center;
      justify-content: center;
  justify-items: center;
}

.bar-container {
  width: 200px;
  height: 30px;
  background-color: rgb(218, 75, 75);
  border-radius: 1rem;
  overflow: hidden;
  position: relative;
}

.bar {
  background-color: rgb(135, 238, 135);
  height: 100%;
  width: 0;
}

.hrMinSec {
  width: 6rem;
  font-size: 4rem;

  display: grid;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
}

.numOfLoops {
  z-index: 100;
  font-size: large;
  display: grid;
  justify-self: center;
  position: absolute;
  top: 50%;
  left:50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.radioItems {
  display: -ms-flexbox;
  display: flex;
  justify-self: center;
  -ms-flex-pack: center;
      justify-content: center;
  justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-item-align: center;
      align-self: center;
  font-size: .9rem;
  font-weight: 800;
  margin: 2rem;
}

.radio {
  padding: .25rem;
}


